import React, { useState } from "react";
import "./App.css";
import Contact from "./components/Contact";
import Experience from "./components/Experience";
import Header from "./components/Header";
import Projects from "./components/Projects";
import WelcomePage from "./components/WelcomePage";
import { Waypoint } from "react-waypoint";

function App() {
  const [navbarText, setNavbarText] = useState("hello!");
  const [section, setSection] = useState("one");

  return (
    <div className="App">
      <div className="main-container">
        <Header
          navbarText={navbarText}
          setNavbarText={setNavbarText}
          section={section}
        />
        <div className="scroll">
          <section className="section s1">
            <WelcomePage />
            <Waypoint
              onEnter={() => {
                setNavbarText("hello!");
                setSection("one");
              }}
            />
          </section>
          <section className="section s2">
            <Experience />
            <Waypoint
              onEnter={() => {
                setNavbarText("doświadczenie");
                setSection("two");
              }}
            />
          </section>
          <section className="section s3">
            <Projects />
            <Waypoint
              onEnter={() => {
                setNavbarText("projekty");
                setSection("three");
              }}
            />
          </section>
          <section className="section s4">
            <Contact />
            <Waypoint
              onEnter={() => {
                setNavbarText("kontakt");
                setSection("four");
              }}
              onLeave={() => {
                setNavbarText("kontakt");
                setSection("four");
              }}
            />
          </section>
        </div>
      </div>
    </div>
  );
}

export default App;
