import React, { useEffect, useState } from "react";
import shopshopshop from "./imgs/shopshopshop.png";
import flags from "./imgs/flags.png";
import minutnik from "./imgs/minutnik.png";
import kalkulator from "./imgs/kalkulator.png";
import pokedex from "./imgs/pokedex.png";
import drums from "./imgs/drums.png";
import maze from "./imgs/maze.png";
import ProjectsList from "./ProjectsList";
import parse from "html-react-parser";

type Props = {
  project: string;
  setProject: React.Dispatch<React.SetStateAction<string>>;
};

const ProjectCard = ({ project, setProject }: Props) => {
  const [data, setData] = useState<
    | {
        id: string;
        image: any;
        name: string;
        stack: string;
        previewURL: string;
        codeURL: string;
        opis: string;
      }
    | undefined
  >();

  useEffect(() => {
    const projectData = ProjectsList.find((element) => {
      return element.id === project;
    });
    setData(projectData);
  }, []);

  if (data) {
    return (
      <div className="project-background">
        <div className="card-container">
          <div className="card-grid">
            <div className="cardimg">
              <img src={data.image} alt="" />
            </div>
            <div className="text-container">
              <div className="close" onClick={() => setProject("")}>
                /zamknij
              </div>

              <div className="projectname">
                <h2>{data.name}</h2>
              </div>
              <div className="projectstack">
                <h5>{parse(data.stack)}</h5>
              </div>
              <div className="other">
                <p>{data.opis}</p>
              </div>
              <div className="buttons">
                <button
                  className="preview"
                  onClick={(e) => {
                    e.preventDefault();
                    window.open(`${data.previewURL}`, "_blank");
                  }}
                >
                  /podgląd/
                </button>
                <button
                  className="code"
                  onClick={(e) => {
                    e.preventDefault();
                    window.location.href = `${data.codeURL}`;
                  }}
                >
                  /kod/
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  } else {
    return <div className="error">ERROR</div>;
  }
};

export default ProjectCard;
